import React, { useState, useEffect } from 'react';
import LoadmoreIcon from './Assets/Icons/loadmore.png';
import './App.css';
import {
  InputGroup,
  Input,
  InputGroupAddon,
  Button,
  Spinner
} from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import BookCard from './BookCard.jsx';
import axios from 'axios';


function App() {
  // States
  const [maxResults, setMaxResults] = useState(8);
  const [search, setSearch] = useState('');
  const [query, setQuery] = useState('javascript');
  const [loading, setLoading] = useState(true);
  const [allBooksLoaded, setallBooksLoaded] = useState(false);
  const [books, setBooks] = useState([]);
  const [queryTEMP, setqueryTEMP ] = useState('javascript');

  // Handle Search


 useEffect(() => {
  load(maxResults, query);
// eslint-disable-next-line react-hooks/exhaustive-deps
}, [query])
 



 const load = (limit, query) => {
  axios
    .get(`https://www.googleapis.com/books/v1/volumes?q=${query}&maxResults=${limit}`)
    .then((res) => {
      if (res.data.items.length > 0) {
        setBooks(res.data.items);
        console.log(res.data.items);
        setLoading(false);
      }
    }).catch(e => {
        setallBooksLoaded(true)
      toast.success('All Books Loaded')

  })
}
const loadMore = () => {
  let newResult = maxResults;
  newResult += 8;
  setMaxResults(newResult);
  load(newResult, queryTEMP);
}


  const handleSubmit = (e) => {
      
    if(!search) {
     toast.error("please enter something to search");
     e.preventDefault();
    }else{
      setLoading(true);
      e.preventDefault();
      setqueryTEMP(search);
      setQuery( search );
      setSearch( " " );
      setTimeout(()=>{
        setLoading(false);
      },1000)
    }
  
    
  }


  const updateSearch = (e) => {
    setSearch( e.target.value );
}
  // Main Show Case
  const mainHeader = () => {
    return (
      <div className='main-image d-flex justify-content-center align-items-center flex-column'>
        {/* Overlay */}
        <div className='filter'/>
        <h1
          className='brand-name'
          style={{ zIndex: 2 }}
        >
          Book-Flix
        </h1>
        <div style={{ width:'100%', zIndex: 2 }}>
          <form style={{
              width: '90%',
              maxWidth: '600px',
              margin:'auto'
          }} onSubmit={handleSubmit}>
          <InputGroup size='lg' className='mb-3'>
            <Input
              placeholder='Book Search'
              value={search}
              onChange={updateSearch}
            />
            <InputGroupAddon addonType='append'>
              <Button color='secondary'>
                <i className='fas fa-search'/>
              </Button>
            </InputGroupAddon>
          </InputGroup>
          </form>
        </div>
      </div>
    );
  };
  


  const handleCards = () => {
    if (loading) {
      return (

          <Spinner style={{ width: '3rem', height: '3rem' }} />

      );
    } else {
      const items = books.map((item, i) => {
        let thumbnail = '';
        if (item.volumeInfo.imageLinks) {
          thumbnail = item.volumeInfo.imageLinks.thumbnail;
        }

        return (

            <BookCard
                key={item.id}
              thumbnail={thumbnail}
              title={item.volumeInfo.title}
              pageCount={item.volumeInfo.pageCount}
              language={item.volumeInfo.language}
              authors={item.volumeInfo.authors}
              publisher={item.volumeInfo.publisher}
              description={item.volumeInfo.description}
              previewLink={item.volumeInfo.previewLink}
              infoLink={item.volumeInfo.infoLink}
              retailPrice={item.saleInfo.retailPrice ? item.saleInfo.retailPrice.amount : null}
              currencyCode={item.saleInfo.retailPrice ? item.saleInfo.retailPrice.currencyCode: null}
              saleability={item.saleInfo.saleability}
            />

        );
      });
      return items;
    }
  };


  return (
    <div>
      {mainHeader()}
      <div className="grid-container">
          {handleCards()}
          {allBooksLoaded || loading ? null : <div className='card loadmore-div' onClick={loadMore}>
              <img className='loadmore-icon' src={LoadmoreIcon} alt="loadmoreicon"/>
              <p className='laodmore-text'>Load More Books</p>
          </div>}

      </div>


      <ToastContainer />
    </div>
  );
}

export default App;
