import React, {useState} from 'react';
import StripeCheckout from 'react-stripe-checkout';
import { Modal} from 'reactstrap';


const handleToken = (token) => {
    console.log({token})
}

const BookCard = ({
                      thumbnail,
                      title,
                      pageCount,
                      language,
                      description,
                      authors,
                      publisher,
                      previewLink,
                      infoLink,
                      retailPrice,
                      saleability,
                      currencyCode
                  }) => {
    // States
    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);


    return (
        <div onClick={toggle} className='card'>
            <img className='book-img' src={thumbnail} alt={title}/>
            <h6 className='title'>{title}</h6>
            <Modal style={{maxWidth: '850px'}} isOpen={modal} toggle={toggle}>
                <div className='modal-header d-flex justify-content-center'>
                    <h5 className='modal-title text-center' id='exampleModalLabel'>
                        {title}
                    </h5>
                    <button
                        aria-label='Close'
                        className='close'
                        type='button'
                        onClick={toggle}
                    >
                        <span aria-hidden={true}>X</span>
                    </button>
                </div>
                <div className='modal-body'>
                    <div className='details-flex-div'>
                        <img src={thumbnail} alt={title} style={{height: '233px', width: '198px'}}/>
                        <div>
                            <p>Page Count: <span className='bold-values'>{pageCount}</span></p>
                            <p>Language : <span className='bold-values'>{language}</span></p>
                            <p>Authors : <span className='bold-values'>{authors}</span></p>
                            <p>Publisher : <span className='bold-values'>{publisher}</span></p>
                            <p>Saleability : <span className='bold-values'>{saleability}</span></p>
                            {retailPrice ?
                                <p>Price : <span className='bold-values'>{retailPrice} {currencyCode}</span></p> : null}
                        </div>
                    </div>
                    <div className='description-div'>
                        <h5>Description</h5>
                        {description}
                    </div>
                </div>
                <div className='modal-footer'>
                    <div className="button-control-div">
                        <button className='default-btn preview-btn'><a className='link-style'
                                                                       href={previewLink}>Preview</a></button>
                        <button className='default-btn info-btn'><a className='link-style' href={infoLink}>Info</a>
                        </button>
                        {saleability === "FOR_SALE" ? <StripeCheckout
                            stripeKey="pk_test_qz2a6sgF0mjXN5kx0hV01VmF006RDRX7fB"
                            token={handleToken}
                            amount={retailPrice * 100}
                            currency="INR"
                            name={title}
                        >
                            <button className="buy-btn default-btn">
                                Buy
                            </button>
                        </StripeCheckout> : null}

                    </div>
                </div>
            </Modal>
        </div>
    );
};

export default BookCard;
